import React from 'react';
import { Modal, Divider, Descriptions, List, Avatar } from 'antd';
import { format } from 'date-fns';

import { useConfigCache } from '../../../utils/Constants';

const OrderInfo = (props) => {
  const { order, closeModal, visible, ...restProps } = props;
  const configCache = useConfigCache();

  const orderItems = (item) => {
    let title = item.product.name;
    let variant = "";
    console.log('item',item)
    let variantKeys = Object.keys(item.variant);
    variantKeys.map((aKey, index)=>{
      variant += `${item.variant[aKey].name}: ${item.variant[aKey].value}${index == variantKeys.length -1 ? "" : ", "}`
    })

    let imageSrc = "";
    if (configCache && item.product.image) {
      imageSrc = configCache.imageSrc + item.product.image;
    }
    return (
      <List.Item
        actions={[
          "qty: " + item.qty,
          "price: " + item.price
        ]}
      >
        <List.Item.Meta
          avatar={
            <Avatar shape="square" src={imageSrc} />
          }
          title={title}
          description={variant}
        />
      </List.Item>
    )
  }

  return (
    <Modal
      title={"Order"}
      width={'95%'}
      visible={visible}
      onCancel={closeModal}
      footer={null}
      //destroyOnClose
      wrapClassName={'products-modalWrapper'}
      style={{overflow:"hidden"}}
    >
    {
      order ?
      <React.Fragment>
        <Divider orientation="left"></Divider>
        <Descriptions 
            size="small"
            bordered
            column={{ xxl: 2, xl: 2, lg: 2, md: 2, sm: 1, xs: 1 }}
        >
            <Descriptions.Item label="">{order._id}</Descriptions.Item>
            <Descriptions.Item label="">{format(new Date(order.createdAt), "MM/dd/yyyy hh:mm:ss aa")}</Descriptions.Item>
            <Descriptions.Item label="">
                {
                  order.paid ? 
                    <span style={{"color":"green"}}></span> : <span style={{"color":"red"}}></span>
                }
            </Descriptions.Item>
            <Descriptions.Item label="">{order.sentOut ? <span style={{"color":"green"}}> <small> (: {order.trackingNum})</small></span>:<span style={{"color":"red"}}></span>}</Descriptions.Item>
        </Descriptions>
        <Divider orientation="left"></Divider>
        <Descriptions 
            id="buyerInfoTable"
            size="small"
            bordered
            column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
        >
            <Descriptions.Item label="">{order.customer.name}</Descriptions.Item>
            <Descriptions.Item label="">{order.customer.contact}</Descriptions.Item>
            <Descriptions.Item label="">{order.customer.address}</Descriptions.Item>
            <Descriptions.Item label="">{order.customer.postcode}</Descriptions.Item>
            <Descriptions.Item label="">{order.customer.province}</Descriptions.Item>
        </Descriptions>
        <Divider orientation="left"></Divider> 
        <List
          itemLayout="horizontal"
          dataSource={order.items}
          renderItem={orderItems}
          //bordered
          footer={(
            <div className="orderInfo-item-summary">
              <Descriptions 
                bordered={true}
                size="small"
                column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1 }}
                style={{maxWidth:"100%"}}
              >
                <Descriptions.Item label={""}>{order.deliveryFee ? order.deliveryFee : 0}</Descriptions.Item>
                <Descriptions.Item label="">{order.total}</Descriptions.Item>
              </Descriptions>
            </div>
          )}
        />
        <div className="orderInfo-extra">
          {configCache.paymentQRImage ? 
            <div style={{textAlign:'center',flexGrow:1}}>
              <img src={configCache.imageSrc + configCache.paymentQRImage} />
            </div>
            : null
          }
        {
          order.sentOut && order.trackingNum ? (
            <div style={{textAlign:'center',flexGrow:1}}>
              <iframe src={`https://m.kuaidi100.com/app/query/?coname=indexall&nu=${order.trackingNum}`} style={{border:'none', maxWidth:'400px', height: '600px', textAlign:'center'}}></iframe>
            </div>
          ) : null
        }
        </div>
      </React.Fragment>
      : "Not found"
    }
  
    </Modal>
  )
}

export default OrderInfo;